.orderdetail_banner {
  margin-top: 90px;
  background-image: url(../../../Images/Banner/bookstore-space-title-area-img-1.jpg);
  height: 300px;
  background-color: #fbf9f6;
  background-repeat: no-repeat;
  background-position: center 0;
  @include flex(center, center);
  @include tablet {
    margin-top: $header-tablet-height;
  }
  @include mobile {
    margin-top: $header-mobile-height;
  }
  &_title {
    text-align: center;
    h6 {
      display: block;
      margin: 8px 0 0;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      color: $white;
      font-size: 12px;
      letter-spacing: 0.35em;
      font-family: "Josefin Sans", sans-serif;
      text-transform: uppercase;
      font-weight: 500;
    }
    h2 {
      font-size: 35px;
      line-height: 1.142em;
      color: $white;
      font-weight: 500;
      letter-spacing: 0.01em;
      -ms-word-wrap: break-word;
      word-wrap: break-word;
    }
  }
}
.orderdetail-content {
  margin: 0 auto;
  font-family: "Josefin Sans", sans-serif;
  @include large{
    width: 100%;
  }
  h2 {
    text-align: center;
    font-size: 35px;
    line-height: 1.142em;
    font-weight: 500;
    letter-spacing: 0.01em;
    font-family: $font;
  }
  &_title,
  &_phone,
  &_price,
  &_address,
  &_product {
    font-size: 12px;
    letter-spacing: 0.35em;
    text-transform: uppercase;
    font-weight: 500;
    line-height: 2rem;
  }
  &_product {
    margin: 20px 0;
    h3 {
      padding: 20px 0;
      border-bottom: 1px solid #e1e1e1;
    }
    &_list {
      width: 100%;
      padding: 20px 0;
      display: none;
      &.show {
        display: block;
        overflow: hidden;
        animation: run 0.8s;

        @keyframes run {
          from {
            height: 0;
            padding: 0;
          }
          to {
            height: 100%;
          }
        }
      }
      &_table {
        margin: 0 0 40px;
        border-collapse: collapse;
        text-transform: capitalize;
        font-family: $font;
        border-spacing: 0;
        vertical-align: middle;
        width: 100%;
        text-align: left;
        tbody {
          tr {
            border: 0;
            border-bottom: 1px solid #e1e1e1;
            th {
              width: 30%;
              padding-right: 5px;
              border: 0;
              color: #000;
              font-weight: 500;
              letter-spacing: 0.01em;
              word-wrap: break-word;
              font-size: 19px;
              line-height: 1.19em;
              margin: 0;
              padding: 14px 0;
              text-align: left;
              vertical-align: middle;
            }
            td {
              padding: 14px 0;
              text-align: left;
              vertical-align: middle;
              font-size: 19px;
            }
          }
        }
      }
      .ListOrder {
        width: 100%;
        .BoxItemCartOrder {
          display: flex;
          padding: 0 10px;
          height: max-content;
          padding: 20px 0;
          flex-wrap: wrap;
          &_content {
            @include flex(center, center);
            flex-wrap: wrap;
            font-size: 20px;
            @include mobile{
              width: 100%;
            }
            &_item {
              padding: 0 20px;
              p {
                margin: 0;
                letter-spacing: 1px;
                text-transform: none;
              }
              &_name {
                font-weight: 500;
                font-size: 20px;
      
                text-transform: capitalize;
              }
              &_price,
              &_quantity {
                font-size: 17px;
                font-weight:normal;
              }
            }
          }
        }
      }
    }
  }
}

