  100px 100px 80px rgba(0, 0, 0, 0.07)
;
}

.ant-card-head-title {
  font-weight: bold;
}

.BoxTexChapter {
  display: none;
}
.RemoveRole {
  display: flex;
  flex-direction: column-reverse;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: stretch;
}
.GmailConfirm{
  margin-top: 10px;
  
}
.GmailConfirm a{
  padding: 10px 0;background: #000;color: #fff;text-decoration: none;
}
.CatchError{
  color: red;
  
}
.CatchError p{
  margin: 0
}
.TabOrderInfor{
  display: block!important;
}


